export default {
	data() {
		return {
			
		};
	},
	props: {
		data: {
			type: Object,
			default: ()=>({})
		},
	},
	methods:{
		show(){
			const { id=1, collectionJobStatus } = this.data
			this.$emit('show',collectionJobStatus,id)
		},
		showDetail(){
			const id = this.data.id || 1
			this.$emit('showDetail',id)
		},
		showDeliveryDetail(){
			const id = this.data.id || 1
			this.$emit('showDeliveryDetail',id)
		},
		recommend(){
			const id = this.data.id || 1
			this.$emit('recommend',id)
		}
	}
}
